/* globals */
*{
  font-family:sans-serif;
}
/* vars */
$dark-grey: #222;
$light-grey: #AAA;
$very-light-grey: #EEE;

body {
  color:$dark-grey;
  font-weight:lighter;
}

ul {
  list-style-type:none;

}

a {
  color:inherit;
  text-decoration:none;
}
a:hover {
  text-decoration:underline;
}

h3 {
  font-weight:normal;
}
/* end globals */

/* nav navbar menu */

.menu {
  position:fixed;
  top:0;
  padding-top:0.5rem;
  padding-bottom:0.5rem;
  padding-right:0.5rem;
  background-color:white;
  width:100%;
}

.navbar ul, li{
    display:inline;
}
.navbar ul {
  padding-inline-start:1rem;
}

.current {
  background-color:magenta;
}
/* end nav navbar menu */

/* post year individual */
.post-content {
  display:block;
  margin-top:3.5rem;
  margin-bottom:1.8rem;
}

div.post-heading li, p, h3{
  padding-inline-start:40px;
  display: inline-block;
  margin-bottom:0;
}

.post-content p, h3{
  padding-inline-start:40px;
  //margin-top:0;
}

.post-content li {
  display:block;
}
/*
.post-content ul {
  margin:0;
}
*/
.post-footer {
  background-color:white;
  display:block;
  position: fixed;
  left:0;
  right:0;
  margin-left:auto;
  margin-right:auto;
  width:80%;  
  bottom:0;
  margin-bottom:1rem;
}

.page-navigation {
  width:100%;
  display:inline-block;
}
.page-navigation a.prev {
  // display:inline-block;
  float:left;
}

.page-navigation a.next {
  // display:inline-block;
  float:right;
}
/* end post year individual */

/* about page */
.site-info ul {
  //width:100%;
  text-align:center;
  position:fixed;
  bottom:0;
  left: 0;
  right: 0;
  padding-inline-start:0;
  margin-bottom:4rem;
}
.site-info li{
  font-size:80%;
  display: inline;
  padding:0.25rem;
}
/* end about page */

/* breadcrumbs */
#breadcrumbs {
  font-size:80%;
  width:100%;
  text-align:center;
  // position:fixed;
  // bottom:0;
  // left: 0;
  // right: 0;
  // padding-bottom:1rem;
  background-color:white;
}
